





















import flSignin from '../../components/Signin/Signin.vue'
export default {
	components: {
		flSignin,
	},
	data() {
		return {
			// 是否可以选择日期
			choose_date: false,
			// 当前日期
			date: "2019-03",
			// 签到成功列表
			list: ["2019-03-10", "03-15", "20"],
			// 是否开启补签
			repair: false,
			// 是否开启补签规则
			rule: false,
		}
	},
	methods: {
		// 签到时触发
		signin_fun(res) {
			console.log(res)
			this.list.push(res.day)
			uni.showToast({
				title: "签到成功",
				icon: "none"
			})
		},
		// 补签成功时触发
		repair_fun(res) {
			console.log(res)
			this.list.push(res.day)
			uni.showToast({
				title: "补签成功",
				icon: "none"
			})
		},
		// 当月份改变时触发
		date_change(date) {
			console.log(date);
			// 更新当前日期
			this.date = date;
			// 更新签到列表
			let list = ["01", "02"];
			this.list = list;
		},
	}
}
